.form{
    width: 50%;
    background-color: #333;
    color: white;
    padding: 0.5rem;
    border-radius: 0.6rem;
    margin-bottom: 0.5rem;
}

.form-field{
    margin: 1rem;
    display: flex;
}

.form-field label{
    flex-basis: 20%;
}

.form-field input{
    flex-basis: 80%;
    border: none;
    border-radius: 00.6rem;
    padding: 0.2rem;
}

.form-field textarea{
    flex-basis: 80%;
    resize: none;
    border: none;
    border-radius: 0.6rem;
    padding: 0.2rem;
}

.form button{
    border: none;
    border-radius: 0.6rem;
    cursor: pointer;
    display: block;
    margin: 0 auto;
    width: 20%;
    padding: 0.5rem 0 0.5rem 0;
}

.form button:hover{
    background-color: rgb(28, 105, 25);
    color: white;
}