.todo{
    background-color: #222;
    margin: 0.5rem;
    border-radius:0.6rem ;
    justify-content:space-around;
    align-items: center;
    padding: 0.5rem;
    display: flex;
}

.todo:hover{
    background-color: #111;
}

.todo p{
    opacity: 0.6;
}

.btn{
    border: none;
    background-color: transparent;
    color: red;
    cursor: pointer;
}